class Table extends Component {
  render() {
      var heading = this.props.heading;
      var body = this.props.body;
      return (
          <table style={{ width: 500 }}>
              <thead>
                  <tr bgcolor = "lightgray">
                      {heading.map(head => <th>{head}</th>) }<th></th>
                  </tr>
              </thead>
              <tbody> 
                  {body.map(row => <TableRow row={row} />)}
              </tbody>
          </table>
      );
  }
}

//this.setState({ corretorNome: row[0] }),this.corretoresEdit , alert(row[0])} }>
class TableRow extends Component {
  render() {
      var row = this.props.row;
      return (
          <tr bgcolor = {row[2]} align='center' editable = "true" 
              onClick={() => {  teste = row[0], page = 3, alert(teste), this.setState({message: "zzz"}) }}>
              {row.map(val => <td> {val}</td>)}
              <td><button onClick={()=>{ this.corretoresGo}}>alert</button></td>
          </tr>
      )
  }
}

