function ListingsScreen({ navigation }) {
  var [listings, setListings] = useState([]);

  //listings = DemoData.corretores;
  for (let i=1; i<DemoData.corretores.length; i++) { 
    var object = DemoData.corretores[i]; 
    listings.push({
      id: object.id
      , name: object.name
      , bgColor: object.bgColor
      , qtdPlantoesSemana: object.qtdPlantoesSemana
    })
  }

  const [isFetching, setIsFetching] = useState(true);
  //listings = [];
  const fetchData = () => {
    var  listingsAux = [];
    //console.log("fetchData")

    for (let i=1; i<DemoData.corretores.length; i++) { 
      var object = DemoData.corretores[i]; 
      listingsAux.push({
        id: object.id
        , name: object.name
        , bgColor: object.bgColor
        , qtdPlantoesSemana: object.qtdPlantoesSemana
      })
    }
    
    //console.log(listings.length)
    setListings(listingsAux);
    //dispatch(getAllTopicAction(userParamData));
    setIsFetching(false);
  };

  const onRefresh = () => {
    setIsFetching(true);
    fetchData();
  };

return (
    <Screen style={styles.screen}>
      
      <Image style={styles.logo}  source={require("../assets/GrupoSinop_Logo4.jpeg")} />
      <Button
          title="Atualizar"
          color="black"
          onPress={() => onRefresh()}
        />
      <FlatList style={styles.screen}
        data={listings}
        extraData={listings}
        //numColumns={7}
        keyExtractor={(listing) => listing.id.toString()}
        renderItem={({ item }) => (
          <Card
            title={'Nome:'+item.name}
            subTitle={"id:" + item.id}
            bgColor={item.bgColor}
            qtdPlantoesSemana={'Plantoes: '+item.qtdPlantoesSemana}
            //image={item.image}
            onPress={() => {navigation.navigate(routes.LISTING_EDIT, item)}}
          />
        )}
        refreshing={true}
        onRefresh={() => console.log("REFRESHING")}       
      />
    </Screen>
  );
}

const styles = StyleSheet.create({
  screen: {
    padding: 10,
    margin: 10,
    backgroundColor: colors.light,
  },
  logo: {
    alignSelf:'center',
    width: 500,
    height: 91,
    margin: 20,
  },
});

export default ListingsScreen;
